<template>
  <nav class="app-topnav">
    <div class="container">
      <ul>
        <!-- 登录状态 -->
        <template v-if="$store.getters.token">
          <li>
            <a href="javascript:;"><i class="iconfont icon-user"></i>{{$store.getters.account||$store.getters.nickname}}</a>
          </li>
          <li><a href="javascript:;" @click="loginOut">退出登录</a></li>
        </template>
        <!-- 非登陆状态 -->
        <template v-else>
          <li><a href="javascript:;" @click="$router.push('/login')">请先登录</a></li>
          <li><a href="javascript:;">免费注册</a></li>
        </template>
        <li><a href="javascript:;">我的订单</a></li>
        <li><a href="javascript:;">会员中心</a></li>
        <li><a href="javascript:;">帮助中心</a></li>
        <li><a href="javascript:;">关于我们</a></li>
        <li><a href="javascript:;"><i class="iconfont icon-phone"></i>手机版</a></li>
      </ul>
    </div>
  </nav>
</template>
<script>
import { useStore } from 'vuex'
import { useRouter, useRoute } from 'vue-router'
export default {
  name: 'AppTopnav',
  setup () {
    const store = useStore()
    const router = useRouter()
    const route = useRoute()
    function loginOut () {
      // 清除个人信息
      store.commit('user/clearProfile')
      // 清除购物车数据
      store.commit('cart/changeList', [])
      // console.log(route)
      router.push({
        path: '/login',
        query: {
          redirectUrl: encodeURIComponent(route.fullPath)
        }
      })
    }
    return {
      loginOut
    }
  }
}
</script>
<style scoped lang="less">
.app-topnav {
  background: #333;
  ul {
    display: flex;
    height: 53px;
    justify-content: flex-end;
    align-items: center;
    li {
      a {
        padding: 0 15px;
        color: #cdcdcd;
        line-height: 1;
        display: inline-block;
        i {
          font-size: 14px;
          margin-right: 2px;
        }
        &:hover {
          color: @xtxColor;
        }
      }
      ~ li {
        a {
          border-left: 2px solid #666;
        }
      }
    }
  }
}
</style>
