<template>
  <div class="goods-hot">
    <h3>{{ title }}</h3>
    <!-- 商品区块 -->
    <GoodsItem  v-for="good in hotList" :key="good.id" :good="good" />
  </div>
</template>
<script>
import { findHotGoods } from '@/api/goods'
import { onMounted, ref, computed } from 'vue'
import { useRoute } from 'vue-router'
import GoodsItem from '@/views/Category/components/good-item.vue'
export default {
  name: 'GoodsHot',
  props: {
    type: {
      type: Number,
      default: 1
    }
  },
  components: {
    GoodsItem
  },
  setup (props) {
    // 处理标题
    const titleObj = { 1: '24小时热销榜', 2: '周热销榜', 3: '总热销榜' }
    const title = computed(() => {
      return titleObj[props.type]
    })
    const hotList = ref({})
    const route = useRoute()
    async function getHotList () { // 获取热销数据
      const res = await findHotGoods({ id: route.params.id, type: props.type })
      // console.log(res)
      hotList.value = res.result
    }
    onMounted(() => {
      getHotList()
    })
    return {
      hotList,
      title,
      titleObj
    }
  }
}
</script>
<style scoped lang="less">
.goods-hot {
  h3 {
    height: 70px;
    background: @helpColor;
    color: #fff;
    font-size: 18px;
    line-height: 70px;
    padding-left: 25px;
    margin-bottom: 10px;
    font-weight: normal;
  }
}
</style>
