<template>
  <div class="xtx-cart-page">
    <div class="container">
      <XtxBread>
        <XtxBreadItem to="/">首页</XtxBreadItem>
        <XtxBreadItem>购物车</XtxBreadItem>
      </XtxBread>
      <div class="cart">
        <table>
          <thead>
            <tr>
              <th width="120"><XtxCheckbox :modelValue="$store.getters['cart/isAll']" @change="allChange">全选</XtxCheckbox></th>
              <th width="400">商品信息</th>
              <th width="220">单价</th>
              <th width="180">数量</th>
              <th width="180">小计</th>
              <th width="140">操作</th>
            </tr>
          </thead>
          <!-- 有效商品 -->
          <tbody>
            <tr v-for="i in $store.getters['cart/effectiveList']" :key="i.skuId">
              <td><XtxCheckbox :modelValue="i.selected" @change="(bool)=>singleChange(i,bool)" /></td>
              <td>
                <div class="goods">
                  <RouterLink to="/"><img :src="i.picture" alt=""></RouterLink>
                  <div>
                    <p class="name ellipsis">{{i.name}}</p>
                    <!-- 选择规格组件 -->
                    <p class="attr">{{i.attrsText}}</p>
                  </div>
                </div>
              </td>
              <td class="tc">
                <p>&yen;{{i.nowPrice}}</p>
                <p>比加入时降价 <span class="red">&yen;{{i.price-i.nowPrice}}</span></p>
              </td>
              <td class="tc">
                <XtxNumbox :modelValue="i.count"
                  @change="(num) => numChange(i, num)"/>
              </td>
              <td class="tc"><p class="f16 red">&yen;{{i.nowPrice*100*i.count/100}}</p></td>
              <td class="tc">
                <p><a class="green" href="javascript:;" @click="delGood(i)">删除</a></p>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <!-- 操作栏 -->
      <div class="action">
        <div class="batch">
          共 {{$store.getters['cart/effectiveList'].length}} 件商品，已选择 {{$store.getters['cart/selectedList'].length}} 件，商品合计：
          <span class="red">¥
            {{$store.getters['cart/allSelectedPrice'].toFixed(2)}}
            </span>
        </div>
        <div class="total">
          <XtxButton type="primary" @click="goPay">下单结算</XtxButton>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { useStore } from 'vuex'
import Message from '@/components/Message'
import { useRouter } from 'vue-router'
export default {
  name: 'XtxCartPage',
  setup () {
    const store = useStore()
    // 全选
    function allChange (bool) {
      store.dispatch('cart/asyncAllChange', bool)
    }
    // 单选
    function singleChange (good, bool) {
      store.dispatch('cart/asyncSingleChange', { good, bool })
    }
    // 修改数量
    function numChange (good, num) {
      store.dispatch('cart/asyncChangeNum', { good, num })
    }
    // 删除商品
    function delGood (good) {
      store.dispatch('cart/asyncDelGood', good)
    }
    // 下单结算
    const router = useRouter()
    function goPay () {
      // 未登录不能跳转
      if (!store.state.user.profile.token) {
        Message({ type: 'warn', text: '请先登录' })
        return false
      }
      // 没有选中商品不能跳转
      if (store.getters['cart/selectedList'].length === 0) {
        Message({ type: 'warn', text: '已选商品数不能为零' })
        return false
      }
      // 跳转到订单页
      router.push('/settlement')
    }
    return {
      allChange,
      singleChange,
      numChange,
      delGood,
      goPay
    }
  }
}
</script>
<style scoped lang="less">
.tc {
  text-align: center;
  .xtx-numbox {
    margin: 0 auto;
    width: 120px;
  }
}
.red {
  color: @priceColor;
}
.green {
  color: @xtxColor
}
.f16 {
  font-size: 16px;
}
.goods {
  display: flex;
  align-items: center;
  img {
    width: 100px;
    height: 100px;
  }
  > div {
    width: 280px;
    font-size: 16px;
    padding-left: 10px;
    .attr {
      font-size: 14px;
      color: #999;
    }
  }
}
.action {
  display: flex;
  background: #fff;
  margin-top: 20px;
  height: 80px;
  align-items: center;
  font-size: 16px;
  justify-content: space-between;
  padding: 0 30px;
  .xtx-checkbox {
    color: #999;
  }
  .batch {
    a {
      margin-left: 20px;
    }
  }
  .red {
    font-size: 18px;
    margin-right: 20px;
    font-weight: bold;
  }
}
.tit {
  color: #666;
  font-size: 16px;
  font-weight: normal;
  line-height: 50px;
}
.xtx-cart-page {
  .cart {
    background: #fff;
    color: #666;
    table {
      border-spacing: 0;
      border-collapse: collapse;
      line-height: 24px;
      th,td{
        padding: 10px;
        border-bottom: 1px solid #f5f5f5;
        &:first-child {
          text-align: left;
          padding-left: 30px;
          color: #999;
        }
      }
      th {
        font-size: 16px;
        font-weight: normal;
        line-height: 50px;
      }
    }
  }
}
</style>
