<template>
  <div class="xtx-city">
    <div class="select" @click="isActive = !isActive">
      <span class="placeholder" v-if="changeResult.countyName===''">请选择配送地址</span>
      <span class="value" v-else>
          {{changeResult.provinceName}}-{{changeResult.cityName}}-{{changeResult.countyName}}
      </span>
      <i class="iconfont icon-angle-down"></i>
    </div>
    <div class="option"  v-if="isActive">
      <span @click="changeCity(item)" class="ellipsis" v-for="item in cityData" :key="item.code">{{item.name}}</span>
    </div>
  </div>
</template>
<script>
import { ref, onMounted, reactive, watch } from 'vue'
import axios from 'axios'
export default {
  name: 'XtxCity',
  emits: ['change'],
  setup (props, ctx) {
    const isActive = ref(false) // 控制选择面板显示隐藏
    const cityData = ref([]) // 城市数据
    const cacheData = ref([]) // 备份数据
    function getCity () { // 获取城市数据
      axios({
        url: 'https://yjy-oss-files.oss-cn-zhangjiakou.aliyuncs.com/tuxian/area.json',
        method: 'get'
      }).then(res => {
        cityData.value = res.data
        // 备份数据再次选择时使用
        cacheData.value = res.data
      })
    }
    onMounted(() => {
      getCity()
    })
    const changeResult = reactive({
      provinceCode: '', // 省code
      provinceName: '', // 省名称
      cityCode: '', // 城市code
      cityName: '', // 城市名称
      countyCode: '', // 地区code
      countyName: '' // 地区名
    })
    function changeCity (curItem) { // 选中城市切换面板数据
      cityData.value = curItem.areaList
      if (curItem.level === 0) {
        changeResult.provinceCode = curItem.code
        changeResult.provinceName = curItem.name
      }
      if (curItem.level === 1) {
        changeResult.cityCode = curItem.code
        changeResult.cityName = curItem.name
      }
      if (curItem.level === 2) {
        changeResult.countyCode = curItem.code
        changeResult.countyName = curItem.name
        // 关闭选择面板
        isActive.value = false
      }
      // 将数据传给父组件
      ctx.emit('change', changeResult)
    }
    watch(// 重新选择城市
      () => isActive.value,
      (newVal) => {
        if (newVal === true) {
          for (const key in changeResult) {
            changeResult[key] = ''
          }
          cityData.value = cacheData.value
        }
      }
    )
    return {
      isActive,
      changeCity,
      cityData,
      changeResult
    }
  }
}
</script>
<style scoped lang="less">
.xtx-city {
  display: inline-block;
  position: relative;
  z-index: 400;
  margin-left: 10px;
  .select {
    border: 1px solid #e4e4e4;
    height: 30px;
    padding: 0 5px;
    line-height: 28px;
    cursor: pointer;
    &.active {
      background: #fff;
    }
    .placeholder {
      color: #999;
    }
    .value {
      color: #666;
      font-size: 12px;
    }
    i {
      font-size: 12px;
      margin-left: 5px;
    }
  }
  .option {
    width: 542px;
    border: 1px solid #e4e4e4;
    position: absolute;
    left: 0;
    top: 29px;
    background: #fff;
    min-height: 30px;
    line-height: 30px;
    display: flex;
    flex-wrap: wrap;
    padding: 10px;
    > span {
      width: 130px;
      text-align: center;
      cursor: pointer;
      border-radius: 4px;
      padding: 0 3px;
      &:hover {
        background: #f5f5f5;
      }
    }
  }
}
</style>
