<template>
  <div class="goods-comment">
    <div class="head">
      <div class="data">
        <p>
          <span>{{ commentInfo.salesCount }}</span
          ><span>人购买</span>
        </p>
        <p>
          <span>{{ commentInfo.praisePercent }}</span
          ><span>好评率</span>
        </p>
        <p>
          <span>{{ commentInfo.evaluateCount }}</span
          ><span>条评价</span>
        </p>
        <p>
          <span>{{ commentInfo.hasPictureCount }}</span>
          <span>条有图评价</span>
        </p>
      </div>
    </div>
    <!-- 排序 -->
    <div class="sort">
      <span>排序：</span>
      <a href="javascript:;"
      :class="{ active: reqParams.sortField === null }"
      @click="changeType(null)"
      >默认</a>
      <a href="javascript:;"
      :class="{ active: reqParams.sortField === 'praiseCount' }"
      @click="changeType('praiseCount')"
      >最热</a>
      <a href="javascript:;"
      :class="{ active: reqParams.sortField === 'createTime' }"
      @click="changeType('createTime')"
      >最新</a>
      <XtxCheckbox style="margin-left: 50px" v-model="reqParams.hasPicture">是否有图</XtxCheckbox>
    </div>
    <!-- 列表 -->
    <div class="list">
      <div class="item" v-for="item in commentList" :key="item.id">
        <div class="user">
          <img :src="item.member.avatar" alt="" />
          <span>{{ item.member.nickname }}</span>
        </div>
        <div class="body">
          <div class="score">
            <i
              v-for="i in item.score"
              :key="i + '1'"
              class="iconfont icon-wjx01"
            ></i>
            <i
              v-for="i in 5 - item.score"
              :key="i + '2'"
              class="iconfont icon-wjx02"
            ></i>
            <span class="attr">{{ item.orderInfo.specsText }}</span>
          </div>
          <div class="text">
            {{ item.content }}
          </div>
          <GoodsCommentImage v-if="reqParams.hasPicture" :pictures="item.pictures"/>
          <div class="time">
            <span>{{ item.createTime }}</span>
            <span class="zan"
              ><i class="iconfont icon-dianzan"></i>
              {{ item.praiseCount }}</span
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { findCommentInfoByGoods, findCommentListByGoods } from '@/api/goods'
import { onMounted, ref, reactive } from 'vue'
import { useRoute } from 'vue-router'
import GoodsCommentImage from './goods-comment-image.vue'
export default {
  name: 'GoodsComment',
  components: {
    GoodsCommentImage
  },
  setup () {
    const commentInfo = ref({})
    const route = useRoute()
    async function getCommentInfo () { // 获取评论数据
      const res = await findCommentInfoByGoods(route.params.id)
      commentInfo.value = res.result
    }
    onMounted(() => {
      getCommentInfo()
    })

    const reqParams = reactive({
      page: 1,
      pageSize: 10,
      hasPicture: false, // 是否有图
      sortField: null
    })
    const commentList = ref([])
    async function getCommentList () { // 获取评论列表
      const res = await findCommentListByGoods(route.params.id, reqParams)
      commentList.value = res.result.items
    }
    onMounted(() => {
      getCommentList()
    })

    function changeType (type) { // 切换排序
      reqParams.sortField = type
      getCommentList()
    }
    return {
      commentInfo,
      commentList,
      reqParams,
      changeType
    }
  }
}
</script>
<style scoped lang="less">
.goods-comment {
  .head {
    display: flex;
    padding: 30px 0;
    .data {
      flex: 1;
      display: flex;
      padding: 20px;
      p {
        flex: 1;
        text-align: center;
        span {
          display: block;
          &:first-child {
            font-size: 32px;
            color: @priceColor;
          }
          &:last-child {
            color: #999;
          }
        }
      }
    }
  }
  .sort {
    height: 60px;
    line-height: 60px;
    border-top: 1px solid #f5f5f5;
    border-bottom: 1px solid #f5f5f5;
    margin: 0 20px;
    color: #666;
    > span {
      margin-left: 20px;
    }
    > a {
      margin-left: 30px;
      &.active,
      &:hover {
        color: @xtxColor;
      }
    }
  }
}
.list {
  padding: 0 20px;
  .item {
    display: flex;
    padding: 25px 10px;
    border-bottom: 1px solid #f5f5f5;
    .user {
      width: 160px;
      img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        overflow: hidden;
      }
      span {
        padding-left: 10px;
        color: #666;
      }
    }
    .body {
      flex: 1;
      .score {
        line-height: 40px;
        .iconfont {
          color: #ff9240;
          padding-right: 3px;
        }
        .attr {
          padding-left: 10px;
          color: #666;
        }
      }
    }
    .text {
      color: #666;
      line-height: 24px;
    }
    .time {
      color: #999;
      display: flex;
      justify-content: space-between;
      margin-top: 5px;
    }
  }
}
</style>
