<template>
  <div class="top-category">
    <div class="container">
      <!-- 面包屑 -->
      <XtxBread>
        <XtxBreadItem :to="'/'">首页</XtxBreadItem>
        <XtxBreadItem>{{breadName}}</XtxBreadItem>
      </XtxBread>
      <!-- 轮播图 -->
      <XtxSlider :sliders="bannerList"></XtxSlider>
      <!-- 所有二级分类 -->
      <div class="sub-list">
      <h3>全部分类</h3>
      <ul>
        <li v-for="i in categoryList" :key="i.id">
          <a href="javascript:;">
            <img
              :src="i.picture"
            />
            <p>{{i.name}}</p>
          </a>
        </li>
      </ul>
      </div>
      <!-- 分类关联商品 -->
      <div class="ref-goods" v-for="item in categoryList" :key="item.id">
        <div class="head">
          <h3>-  {{item.name}}  -</h3>
          <XtxMore to="/" />
        </div>
        <div class="body">
          <GoodItem v-for="good in item.goods" :key="good.id" :good="good"/>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import { findBanner } from '@/api/home'
import { findTopCategory } from '@/api/category'
import { ref, onMounted } from 'vue'
import { useRoute } from 'vue-router'
import GoodItem from './components/good-item.vue'
export default {
  components: {
    GoodItem
  },
  name: 'TopCategory',
  setup () {
    // 获取轮播图数据
    const bannerList = ref([])
    async function getBannerList () {
      const res = await findBanner()
      bannerList.value = res.result
    }
    onMounted(() => {
      getBannerList()
    })
    // 获取二级分类数据
    const route = useRoute()
    const categoryList = ref([])
    // 面包屑对应名称
    const breadName = ref('')
    async function getCategoryList (id = route.params.id) {
      const res = await findTopCategory(id)
      categoryList.value = res.result.children
      breadName.value = res.result.name
    }
    onMounted(() => {
      getCategoryList()
    })

    return {
      bannerList,
      categoryList,
      breadName
    }
  }
}
</script>

<style scoped lang="less">
.top-category {
  h3 {
    font-size: 28px;
    color: #666;
    font-weight: normal;
    text-align: center;
    line-height: 100px;
  }
  .sub-list {
    margin-top: 20px;
    background-color: #fff;
    ul {
      display: flex;
      padding: 0 32px;
      flex-wrap: wrap;
      li {
        width: 168px;
        height: 160px;
        a {
          text-align: center;
          display: block;
          font-size: 16px;
          img {
            width: 100px;
            height: 100px;
          }
          p {
            line-height: 40px;
          }
          &:hover {
            color: @xtxColor;
          }
        }
      }
    }
  }
  .ref-goods {
    background-color: #fff;
    margin-top: 20px;
    position: relative;
    .head {
      .xtx-more {
        position: absolute;
        top: 20px;
        right: 20px;
      }
      .tag {
        text-align: center;
        color: #999;
        font-size: 20px;
        position: relative;
        top: -20px;
      }
    }
    .body {
      display: flex;
      justify-content: space-around;
      padding: 0 40px 30px;
    }
  }
}
</style>
