import { findSubCategoryFilter } from '@/api/category'
import { onMounted, ref } from 'vue'
import { useRoute } from 'vue-router'
export function useCategory () {
  const route = useRoute()
  // 筛选区
  const subCategoryObj = ref({})
  async function getFilterCategory (id = route.params.id) {
    const res = await findSubCategoryFilter(id)
    subCategoryObj.value = res.result
    // 初始化获取到的数据
    subCategoryObj.value.brands.unshift({
      name: '全部',
      id: null
    })
    subCategoryObj.value.saleProperties.forEach((item) => {
      item.properties.unshift({
        name: '全部',
        id: null
      })
      // 给每一个规格添加一个专门用来控制是否激活的字段 selected true 激活 false 不激活
      item.properties.forEach(item => {
        item.selected = false
      })
    })
  }
  onMounted(() => {
    getFilterCategory()
  })
  return {
    subCategoryObj
  }
}
