<template>
  <div class="goods-tabs">
    <nav>
      <a href="javascript:;" :class="{active: activeType==='detail'}" @click="changeTab('detail')">商品详情</a>
      <a href="javascript:;" :class="{active: activeType==='comment'}" @click="changeTab('comment')">商品评价</a>
    </nav>
    <!-- 这个位置显示对应的组件 GoodsDetail 或者 GoodsComment -->
    <!-- 方法一 -->
    <!-- <GoodsDetail v-if="activeType==='detail'"/>
    <GoodsComment v-else/> -->
    <!-- 方法二 -->
    <!-- <component :is="activeType==='detail'? 'GoodsDetail':'GoodsComment'"></component> -->
    <!-- 方法三 -->
    <!-- 组件缓存,提高性能 -->
    <keep-alive>
        <component :is="tabsType[activeType]"></component>
    </keep-alive>
  </div>
</template>
<script>
import { ref } from 'vue'
import GoodsDetail from './goods-detail.vue'
import GoodsComment from './goods-comment.vue'
const tabsType = {
  detail: GoodsDetail,
  comment: GoodsComment
}
export default {
  name: 'GoodsTabs',
  //   components: { //方法二
  //     GoodsDetail,
  //     GoodsComment
  //   },
  setup () {
    const activeType = ref('detail')
    function changeTab (type) {
      activeType.value = type
    }
    return {
      activeType,
      changeTab,
      tabsType
    }
  }
}
</script>
<style scoped lang="less">
.goods-tabs {
  min-height: 600px;
  background: #fff;
  nav {
    height: 70px;
    line-height: 70px;
    display: flex;
    border-bottom: 1px solid #f5f5f5;
    a {
      padding: 0 40px;
      font-size: 18px;
      position: relative;
      > span {
        color: @priceColor;
        font-size: 16px;
        margin-left: 10px;
      }
      &:first-child {
        border-right: 1px solid #f5f5f5;
      }
      &.active {
        &::before {
          content: "";
          position: absolute;
          left: 40px;
          bottom: -1px;
          width: 72px;
          height: 2px;
          background: @xtxColor;
        }
      }
    }
  }
}
</style>
