<template>
  <LoginHeader>联合登录</LoginHeader>
  <section class="container">
    <nav class="tab">
      <a
        href="javascript:;"
        @click="isBindMobile = true"
        :class="{ active: isBindMobile === true }"
      >
        <i class="iconfont icon-bind" />
        <span>已有小兔鲜账号，请绑定手机</span>
      </a>
      <a
        href="javascript:;"
        @click="isBindMobile = false"
        :class="{ active: isBindMobile === false }"
      >
        <i class="iconfont icon-edit" />
        <span>没有小兔鲜账号，请完善资料</span>
      </a>
    </nav>
    <!-- 绑定手机 -->
    <CallbackBind v-if="isBindMobile" :union-id="unionId" />
    <!-- 补充信息 -->
    <CallbackPatch v-else :union-id="unionId" />
  </section>
  <LoginFooter />
</template>

<script>
import LoginHeader from './components/login-header'
import LoginFooter from './components/login-footer'
import CallbackBind from './callback-bind'
import CallbackPatch from './callback-patch'
import { ref } from 'vue'
import { userQQLogin } from '@/api/user'
import QC from 'qc'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
export default {
  name: 'PageCallback',
  components: { LoginHeader, LoginFooter, CallbackBind, CallbackPatch },
  setup () {
    // 响应式数据 切换这个响应式数据 v-if 匹配
    const isBindMobile = ref(true) // 绑定手机显示
    // 判断当前qq是否已经绑定过
    /**
     * 1.先通过qq三方的接口获取一个 openId
     * 2.把openId作为我们自己的接口参数去调用接口 如果当前接口返回成功状态 跳回首页
     *   如果发现当前接口返回失败状态  代表不是已经注册并且已经绑定 停留在当前页面
     */
    // 如果已经通过扫码或者qq账号登录 成功之后 此方法返回值为true
    console.log(QC.Login.check())
    const unionId = ref('')
    const store = useStore()
    const router = useRouter()
    if (QC.Login.check()) {
      QC.Login.getMe((openId) => {
        unionId.value = openId
        // openId拿到的就是我们需要标识
        // 调用自己的接口校验
        // 当前试图直接使用 openId进行登录发现 接口报错了 没有返回成功状态
        // 当前的账号是没有进行过绑定的
        console.log('当前的openId为', openId)
        userQQLogin(openId).then((res) => {
          console.log('校验当前是否已经绑定过:', res)
          // 1. 把当前获取的用户信息存入vuex保存起来
          store.commit('user/setUser', res.result)
          // 2. 立刻跳转到首页
          router.push('/')
        })
      })
    }
    return {
      isBindMobile,
      unionId
    }
  }
}
</script>

<style scoped lang='less'>
.container {
  padding: 25px 0;
}
.tab {
  background: #fff;
  height: 80px;
  padding-top: 40px;
  font-size: 18px;
  text-align: center;
  a {
    color: #666;
    display: inline-block;
    width: 350px;
    line-height: 40px;
    border-bottom: 2px solid #e4e4e4;
    i {
      font-size: 22px;
      vertical-align: middle;
    }
    span {
      vertical-align: middle;
      margin-left: 4px;
    }
    &.active {
      color: @xtxColor;
      border-color: @xtxColor;
    }
  }
}
.tab-content {
  min-height: 600px;
  background: #fff;
}
</style>
