<template>
  <!-- 顶部通栏 -->
  <TopNav/>
  <!-- header区域 -->
  <Header/>
  <!-- 吸顶组件 -->
  <HeaderSticky/>
  <main>
    <!-- 二级路由出口 -->
    <!--
      解决路由缓存问题方法一
      fullPath:获取path+参数
      path:只获取path
      key作用: 1.提高列表的渲染性能  2.强制更新组件(破坏缓存机制)
     -->
     <!-- 组件销毁重建,并重新请求数据渲染,性能不高 -->
    <router-view :key="$route.fullPath"></router-view>
    <!-- <router-view></router-view> -->
  </main>
  <!-- 底部footer -->
  <Footer/>
</template>

<script>
import TopNav from './components/topnav'
import Header from './components/header'
import Footer from './components/footer'
import HeaderSticky from './components/header-sticky'
export default {
  name: 'xtx-layout',
  components: {
    TopNav,
    Header,
    Footer,
    HeaderSticky
  }
}
</script>

<style scoped lang='less'>
header{
  color: @helpColor;
}
</style>
