<template>
  <div class='sub-category'>
    <div class="container" >
      <!-- 面包屑区域 -->
      <XtxBread>
        <XtxBreadItem to="/">首页</XtxBreadItem>
        <XtxBreadItem :to="`/category/${subCategoryObj.parentId}`">
          {{ subCategoryObj.parentName }}
        </XtxBreadItem>
        <XtxBreadItem>{{ subCategoryObj.name }}</XtxBreadItem>
      </XtxBread>
      <!-- 筛选区域 -->
      <div class="sub-filter">
        <!-- 品牌 -->
        <div class="item">
        <div class="head">品牌：</div>
        <div class="body">
            <a
            href="javascript:;"
            v-for="brand in subCategoryObj.brands"
            :key="brand.id"
            @click="changeBrand(brand.id)"
            >{{ brand.name }}</a
            >
        </div>
        </div>
        <!-- 规格 -->
        <div
        class="item"
        v-for="item in subCategoryObj.saleProperties"
        :key="item.id"
        >
        <div class="head">{{ item.name }}：</div>
        <div class="body">
            <a
            href="javascript:;"
            v-for="i in item.properties"
            :key="i.id"
            @click="toggleSelected(i,item)"
            :class="{ active: i.selected === true }"
            >{{ i.name }}</a
            >
        </div>
        </div>
      </div>
      <!-- 排序区域 -->
      <div class="list-container">
        <div class='sub-sort'>
        <div class="sort">
           <a
            :class="{ active: sortType === null }"
            @click="changeSort(null)"
            href="javascript:;">默认排序</a>
            <a
            :class="{ active: sortType === 'publishTime' }"
            @click="changeSort('publishTime')"
            href="javascript:;">最新商品</a>
            <a
            :class="{ active: sortType === 'orderNum' }"
            @click="changeSort('orderNum')"
            href="javascript:;">最高人气</a>
            <a
            :class="{ active: sortType === 'evaluateNum' }"
            @click="changeSort('evaluateNum')"
            href="javascript:;">评论最多</a>
        </div>
        </div>
        <div class="sort-list-container">
          <GoodsItem  v-for="good in goodsFilterList" :key="good.id" :good="good"/>
        </div>
      </div>
    </div>
    <!-- 加载更多组件 -->
    <XtxInfiniteLoad
      @load="loadNext"
      :isLoading="isLoading"
      :isFinished="isFinished"
    />
  </div>
</template>

<script>

import { useCategory } from './utils/useCategory'
import { useGoods } from './utils/useGoods'
import GoodsItem from '../components/good-item.vue'
export default {
  name: 'SubCategory',
  components: {
    GoodsItem
  },
  setup () {
    const { subCategoryObj } = useCategory()
    const {
      reqParams,
      goodsFilterList,
      sortType,
      changeSort,
      changeBrand,
      toggleSelected,
      loadNext,
      isLoading,
      isFinished
    } = useGoods()
    return {
      subCategoryObj,
      reqParams,
      goodsFilterList,
      sortType,
      changeSort,
      changeBrand,
      toggleSelected,
      loadNext,
      isLoading,
      isFinished
    }
  }
}
</script>
<style scoped lang='less'>
.goods-list {
  background: #fff;
  padding: 0 25px;
  margin-top: 25px;
}
// 筛选区
  .sub-filter {
    background: #fff;
    padding: 25px;
    .item {
      display: flex;
      padding: 10px 0;
      .head {
        width: 80px;
        color: #999;
      }
      .body {
        flex: 1;
        a {
          margin-right: 36px;
          transition: all .3s;
          &.active,
          &:hover {
            color: @xtxColor;
          }
        }
      }
    }
  }
  .list-container {
  margin-top: 20px;
  padding: 0 20px;
  background: #fff;
}
// 排序区域
  .sub-sort {
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .sort {
    display: flex;
    a {
      height: 30px;
      line-height: 28px;
      border: 1px solid #e4e4e4;
      padding: 0 20px;
      margin-right: 20px;
      color: #999;
      border-radius: 2px;
      position: relative;
      transition: all .3s;
      &.active {
        background: @xtxColor;
        border-color: @xtxColor;
        color: #fff;
      }
      .arrow {
        position: absolute;
        border: 5px solid transparent;
        right: 8px;
        &.up {
          top: 3px;
          border-bottom-color: #bbb;
            &.active {
            border-bottom-color: @xtxColor;
          }
        }
        &.down {
          top: 15px;
          border-top-color: #bbb;
          &.active {
            border-top-color: @xtxColor;
          }
        }
      }
    }
  }
  .check {
    .xtx-checkbox {
      margin-left: 20px;
      color: #999;
    }
  }
}
.sort-list-container {
      display: flex;
      flex-wrap: wrap;
      .goods-item {
        flex: 1;
      }
}

</style>
