<template>
  <div class="account-box">
    <Form
      class="form"
      :validation-schema="myValide"
      v-slot="{ errors }"
      autocomplete="off"
      ref="target"
    >
      <div class="form-item">
        <div class="input">
          <i class="iconfont icon-user"></i>
          <Field
            v-model="form.account"
            name="account"
            type="text"
            placeholder="请输入用户名"
            :class="{ error: errors.account }"
          />
        </div>
        <!-- 表单验证错误信息提示 -->
        <div class="error" v-if="errors.account">
          <i class="iconfont icon-warning" />{{ errors.account }}
        </div>
      </div>
      <div class="form-item">
        <div class="input">
          <i class="iconfont icon-lock"></i>
          <Field
            v-model="form.password"
            name="password"
            type="password"
            placeholder="请输入密码"
            :class="{ error: errors.password }"
          />
        </div>
        <!-- 表单验证错误信息提示 -->
        <div class="error" v-if="errors.password">
          <i class="iconfont icon-warning" />{{ errors.password }}
        </div>
      </div>
      <div class="form-item">
        <div class="agree">
          <Field as="XtxCheckbox" name="isAgree" v-model="form.isAgree" />
          <span>我已同意</span>
          <a href="javascript:;">《隐私条款》</a>
          <span>和</span>
          <a href="javascript:;">《服务条款》</a>
        </div>
      </div>
      <a href="javascript:;" class="btn" @click="submit">登录</a>
    </Form>
    <div class="action">
      <a
        href="https://graph.qq.com/oauth2.0/authorize?client_id=100556005&response_type=token&scope=all&redirect_uri=http%3A%2F%2Fwww.corho.com%3A8080%2F%23%2Flogin%2Fcallback"
      >
        <img
          src="https://qzonestyle.gtimg.cn/qzone/vas/opensns/res/img/Connect_logo_7.png"
          alt=""
        />
      </a>
      <!-- // 准备一个id容器 -->
      <!-- <span id="qqLoginBtn"></span> -->
      <div class="url">
        <a href="javascript:;">忘记密码</a>
        <a href="javascript:;">免费注册</a>
      </div>
    </div>
  </div>
</template>
<script>
import { Form, Field } from 'vee-validate'
import validate from '@/utils/vee-validate-schema'
import { reactive, ref, onMounted } from 'vue'
import { useStore } from 'vuex'
import { useRouter, useRoute } from 'vue-router'
import Message from '@/components/Message'
// 导入QC
// import QC from 'qc'
export default {
  name: 'XtxLoginForm',
  components: {
    Form,
    Field
  },
  setup () {
    const target = ref(null)
    // 表单对象数据
    const form = reactive({
      account: null, // 账号
      password: null, // 密码
      isAgree: true // 是否同意协议
    })
    // 校验规则对象
    const myValide = {
      account: validate.account,
      password: validate.password,
      isAgree: validate.isAgree
    }
    // 兜底校验
    const store = useStore()
    const router = useRouter()
    const route = useRoute()
    async function submit () {
      const valid = await target.value.validate()
      // console.log(valid)
      if (valid) {
        store.dispatch('user/asyncGetProfile', form).then(() => {
          Message({ type: 'success', text: '登陆成功!' })
          // 触发action函数进行购物车合并
          store.dispatch('cart/mergeCartList')
          // 跳转回退
          router.push(decodeURIComponent(route.query.redirectUrl || '/'))
        })
      }
    }
    onMounted(() => {
      // 组件渲染完毕，使用QC生成QQ登录按钮
      // QC.Login({
      //   btnId: 'qqLoginBtn'
      // })
    })
    return { form, myValide, target, submit }
  }
}
</script>
<style lang="less" scoped>
// 账号容器
.account-box {
  .toggle {
    padding: 15px 40px;
    text-align: right;
    a {
      color: @xtxColor;
      i {
        font-size: 14px;
      }
    }
  }
  .form {
    padding: 0 20px;
    &-item {
      margin-bottom: 28px;
      .input {
        position: relative;
        height: 36px;
        > i {
          width: 34px;
          height: 34px;
          background: #cfcdcd;
          color: #fff;
          position: absolute;
          left: 1px;
          top: 1px;
          text-align: center;
          line-height: 34px;
          font-size: 18px;
        }
        input {
          padding-left: 44px;
          border: 1px solid #cfcdcd;
          height: 36px;
          line-height: 36px;
          width: 100%;
          &.error {
            border-color: @priceColor;
          }
          &.active,
          &:focus {
            border-color: @xtxColor;
          }
        }
        .code {
          position: absolute;
          right: 1px;
          top: 1px;
          text-align: center;
          line-height: 34px;
          font-size: 14px;
          background: #f5f5f5;
          color: #666;
          width: 90px;
          height: 34px;
          cursor: pointer;
        }
      }
      > .error {
        position: absolute;
        font-size: 12px;
        line-height: 28px;
        color: @priceColor;
        i {
          font-size: 14px;
          margin-right: 2px;
        }
      }
    }
    .agree {
      a {
        color: #069;
      }
    }
    .btn {
      display: block;
      width: 100%;
      height: 40px;
      color: #fff;
      text-align: center;
      line-height: 40px;
      background: @xtxColor;
      &.disabled {
        background: #cfcdcd;
      }
    }
  }
  .action {
    padding: 20px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .url {
      a {
        color: #999;
        margin-left: 10px;
      }
    }
  }
}
</style>
