<template>
  <div class="xtx-form">
    <div class="user-info">
      <img :src="qqInfo.avatar" alt="" />
      <p>
        Hi，{{ qqInfo.nickname }} 欢迎来小兔鲜，完成绑定后可以QQ账号一键登录哦~
      </p>
    </div>
    <Form :validation-schema="mySchema" v-slot="{ errors }" autocomplete="off" ref="formRef">
      <div class="xtx-form-item">
        <div class="field">
          <i class="icon iconfont icon-phone"></i>
          <Field
            :class="{ err: errors.mobile }"
            v-model="form.mobile"
            name="mobile"
            class="input"
            type="text"
            placeholder="绑定的手机号"
          />
          <!-- <input class="input" type="text" placeholder="绑定的手机号" /> -->
        </div>
        <div class="error" v-if="errors.mobile">{{ errors.mobile }}</div>
      </div>
      <div class="xtx-form-item">
        <div class="field">
          <i class="icon iconfont icon-code"></i>
          <Field
            :class="{ err: errors.code }"
            v-model="form.code"
            name="code"
            class="input"
            type="text"
            placeholder="绑定的验证码"
          />
          <!-- <input class="input" type="text" placeholder="短信验证码" /> -->
          <span class="code" @click="send">{{
            curTime === 0 ? "发送验证码" : curTime + "s后发送"
          }}</span>
        </div>
        <div class="error" v-if="errors.code">{{ errors.code }}</div>
      </div>
    </Form>
    <a href="javascript:;" class="submit" @click="bind">立即绑定</a>
  </div>
</template>

<script>
import QC from 'qc'
import { reactive, ref } from 'vue'
import { Form, Field } from 'vee-validate'
import schema from '@/utils/vee-validate-schema'
import { useTime } from '@/compositions'
import { userQQBindCode, userQQBindLogin } from '@/api/user'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import message from '@/components/Message/index.js'
export default {
  name: 'CallbackBind',
  props: {
    unionId: {
      type: String
    }
  },
  components: {
    Form, Field
  },
  setup (props) {
    const qqInfo = reactive({
      nickname: '',
      avatar: ''
    })
    // 获取一下qq账号的用户名和头像 显示到页面中
    // QC.api('get_user_info').success(( res )=>{})  核心方法
    // 这个方法调用之前必须先调用check方法保证已经通过扫码登录了
    if (QC.Login.check()) {
      QC.api('get_user_info').success((res) => {
        // res:拿到当前登录的qq账号的所有信息
        qqInfo.nickname = res.data.nickname
        qqInfo.avatar = res.data.figureurl_qq_2
      })
    }
    // 表单校验
    // 准备校验函数对象 shecma
    const mySchema = {
      mobile: schema.mobile,
      code: schema.code
    }
    // 准备表单数据
    const form = reactive({
      mobile: '',
      code: ''
    })

    // 发送验证码
    // 一开始显示`发送验证码`  点击`发送验证码`  从60开始倒计时
    // 倒计时的过程中到0之前 都不可重复点击 只有等于0的时候倒计时计数  可以再次点击`发送验证码`
    const { curTime, start } = useTime(60)
    // 在正式开启倒计时之前
    // 1.验证手机号是否ok
    // 2.调用一个发送验证码的接口
    function send () {
      if (schema.mobile(form.mobile) === true) {
        // 只有curTime为0的时候才能调用接口
        if (curTime.value === 0) {
          // 调用接口
          userQQBindCode(form.mobile).then(res => {
            start() // 开始倒计时
          })
        }
      }
    }
    // 实现立刻绑定功能
    const formRef = ref(null)
    const store = useStore()
    const router = useRouter()
    async function bind () {
      // 兜底校验
      const res = await formRef.value.validate()
      if (res) {
        userQQBindLogin({ unionId: props.unionId, mobile: form.mobile, code: form.code }).then(res => {
          console.log(res)
          // 1.把当前返回的用户数据通过提交mutation函数存入vuex
          store.commit('user/setUser', res.result)
          // 合并购物车
          // 2.跳转到应用首页
          router.push('/')
          // 3.提示用户绑定成功
          message({ type: 'success', text: '绑定成功' })
        })
      }
    }
    return {
      qqInfo,
      mySchema,
      form,
      curTime,
      send,
      formRef,
      bind
    }
  }
}
</script>

<style scoped lang='less'>
.user-info {
  width: 320px;
  height: 70px;
  margin: 0 auto;
  display: flex;
  background: #f2f2f2;
  align-items: center;
  padding: 0 10px;
  margin-bottom: 25px;
  img {
    background: #f2f2f2;
    width: 50px;
    height: 50px;
  }
  p {
    padding-left: 10px;
  }
}
.code {
  position: absolute;
  right: 0;
  top: 0;
  line-height: 50px;
  width: 80px;
  color: #999;
  &:hover {
    cursor: pointer;
  }
}
</style>
