<template>
  <div class="home-banner">
    <XtxSlider :sliders="sliders" />
  </div>
</template>

<script>
import { findBanner } from '@/api/home'
import { onMounted, ref } from 'vue'
export default {
  name: 'HomeBanner',
  setup () {
    const sliders = ref([])
    async function getSliderList () { // 获取轮播数据
      const res = await findBanner()
      sliders.value = res.result
    }
    onMounted(() => {
      getSliderList()
    })
    return {
      sliders
    }
  }
}
</script>

<style scoped lang='less'>
.home-banner {
  width: 1240px;
  height: 500px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 98;
}
</style>
