<template>
  <div class="box" style="height:500px">
    <div class="xtx-slider">
      <!-- 图片列表 -->
      <ul class="slider-body" @mouseenter="clearTimer" @mouseleave="startTimer">
        <li
          class="slider-item"
          v-for="(item,i) in sliders"
          :key="item.id"
          :class="{ fade: curIndex === i }"
        >
          <img :src="item.imgUrl" alt="" />
        </li>
      </ul>
      <!-- 圆圈切换按钮 -->
      <div class="slider-indicator">
        <span :class="{active:curIndex === index}"
          v-for="(item,index) in sliders"
          :key="index"
          @click="curIndex = index"></span>
      </div>
    </div>
  </div>
</template>

<script>

import { ref, onMounted } from 'vue'
export default {
  name: 'XtxSlider',
  props: {
    sliders: { // 接受轮播数据
      type: Array,
      default: () => { return [] } // 默认为空---复杂类型默认值需要函数返回
    },
    autoPlay: { // 是否自动播放
      type: Boolean,
      default: true // 默认自动播放
    }
  },
  setup (props) {
    const curIndex = ref(0)
    const timer = ref(null)
    function clearTimer () { // 鼠标进入
      clearInterval(timer.value)
    }
    function startTimer () { // 鼠标离开
      tiemrLoop()
    }
    function tiemrLoop () { // 封装定时器自动循环播放
      if (!props.autoPlay) {
        return false
      }
      timer.value = window.setInterval(() => {
        curIndex.value++
        if (curIndex.value >= props.sliders.length) {
          curIndex.value = 0
        }
      }, 2000)
    }
    onMounted(() => {
      tiemrLoop()
    })

    return {
      curIndex,
      clearTimer,
      startTimer
    }
  }
}
</script>

<style scoped lang='less'>
.xtx-slider {
  width: 100%;
  height: 100%;
  min-width: 300px;
  min-height: 150px;
  position: relative;
  .slider {
    &-body {
      width: 100%;
      height: 100%;
    }
    &-item {
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;
      transition: opacity 0.5s linear;
      &.fade {
        opacity: 1;
        z-index: 1;
      }
      img {
        width: 100%;
        height: 100%;
      }
    }
    &-indicator {
      position: absolute;
      left: 0;
      bottom: 20px;
      z-index: 2;
      width: 100%;
      text-align: center;
      span {
        display: inline-block;
        width: 12px;
        height: 12px;
        background: rgba(0, 0, 0, 0.2);
        border-radius: 50%;
        cursor: pointer;
        ~ span {
          margin-left: 12px;
        }
        &.active {
          background: #fff;
        }
      }
    }
    &-btn {
      width: 44px;
      height: 44px;
      background: rgba(0, 0, 0, 0.2);
      color: #fff;
      border-radius: 50%;
      position: absolute;
      top: 228px;
      z-index: 2;
      text-align: center;
      line-height: 44px;
      opacity: 0;
      transition: all 0.5s;
      &.prev {
        left: 20px;
      }
      &.next {
        right: 20px;
      }
    }
  }
  &:hover {
    .slider-btn {
      opacity: 1;
    }
  }
}
</style>
