import { ref, computed } from 'vue'
import { useIntersectionObserver, useIntervalFn } from '@vueuse/core'
import dayjs from 'dayjs'
// 组件数据懒加载逻辑复用
export function useObserver (api) {
  const target = ref(null)
  const { stop } = useIntersectionObserver(
    target,
    ([{ isIntersecting }], observerElement) => {
      if (isIntersecting) {
        api()
        stop()
      }
    }
  )

  return target
}

// 倒计时
export function useTime (time) {
  const curTime = ref(0)
  const countTimeText = computed(() => {
    return dayjs.unix(curTime.value).format('mm分ss秒')
  })
  const { pause, resume } = useIntervalFn(() => {
    if (curTime.value > 0) {
      curTime.value--
    } else {
      pause()
    }
  }, 1000)
  function start (_time) {
    curTime.value = _time || time
    resume()
  }
  return {
    curTime,
    countTimeText,
    start,
    pause
  }
}
