<template>
  <div class='xtx-goods-page'>
    <div class="container" v-if="goodObj.categories">
      <!-- 面包屑 -->
      <XtxBread>
        <XtxBreadItem to="/">首页</XtxBreadItem>
        <XtxBreadItem :to="`/category/${goodObj.categories[1].id}`">{{goodObj.categories[1].name}}</XtxBreadItem>
        <XtxBreadItem :to="`/category/sub/${goodObj.categories[0].id}`">{{goodObj.categories[0].name}}</XtxBreadItem>
        <XtxBreadItem to="/"> {{goodObj.name}}</XtxBreadItem>
      </XtxBread>
      <!-- 商品信息 -->
      <div class="goods-info">
          <!-- 图片预览区 -->
          <div class="media">
              <GoodsImage :image-list="goodObj.mainPictures"/>
              <GoodsSales :good="goodObj"/>
          </div>
          <!-- 商品信息区 -->
          <div class="spec">
            <GoodsName :good="goodObj"/>
            <GoodsSku :goods="goodObj" @change="changeSku"/>
            <XtxNumbox :max="7" :min="1" v-model="num"/>
            <XtxButton size="middle" type="primary"
            @click="insertCart"
            style="margin-top:20px;">加入购物车</XtxButton>
          </div>
      </div>
      <!-- 商品详情 -->
      <div class="goods-footer">
        <div class="goods-article">
        <!-- 商品+评价 -->
        <div class="goods-tabs">
          <GoodsTabs/>
        </div>
        </div>
        <!-- 24热榜+专题推荐 -->
        <div class="goods-aside">
          <GoodsHot :type="1" />
          <GoodsHot :type="2" />
          <GoodsHot :type="3" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { findGoods } from '@/api/goods'
import { ref, onMounted } from 'vue'
import { useRoute } from 'vue-router'
import GoodsImage from './components/goods-image.vue'
import GoodsSales from './components/goods-sales'
import GoodsName from './components/goods-name'
import GoodsSku from './Sku'
import GoodsTabs from './components/goods-tabs.vue'
import GoodsHot from './components/goods-hot.vue'
import { useStore } from 'vuex'
export default {
  name: 'XtxGoodsPage',
  components: {
    GoodsImage,
    GoodsSales,
    GoodsName,
    GoodsSku,
    GoodsTabs,
    GoodsHot
  },
  setup () {
    const goodObj = ref({})
    const route = useRoute()
    async function getGoodObj () { // 获取商品详情数据
      const res = await findGoods(route.params.id)
      goodObj.value = res.result
    }
    onMounted(() => {
      getGoodObj()
    })
    // 接受sku组件数据
    const skuObj = ref({})
    function changeSku (sku) {
      if (sku.skuId) {
        skuObj.value = sku
      }
    }
    // 加入购物车
    const store = useStore()
    // 商品数量
    const num = ref(1)
    function insertCart () {
      // 准备好加入购物车的所有字段数据
      const good = {
        id: goodObj.value.id,
        name: goodObj.value.name,
        picture: goodObj.value.mainPictures[0],
        skuId: skuObj.value.skuId,
        price: skuObj.value.oldPrice,
        nowPrice: skuObj.value.price,
        attrsText: skuObj.value.specsText,
        stock: skuObj.value.inventory,
        selected: true, // 选中
        isEffective: true, // 有效
        count: num.value // 商品数据
      }
      store.dispatch('cart/asyncInsertCart', good)
    }
    return {
      goodObj,
      num,
      changeSku,
      insertCart
    }
  }
}
</script>

<style scoped lang='less'>
.goods-info {
  min-height: 600px;
  background: #fff;
  display: flex;
  .media {
        width: 580px;
        height: 600px;
        padding: 30px 50px;
      }
  .spec {
        flex: 1;
        padding: 30px 30px 30px 0;
      }
}
.goods-footer {
  display: flex;
  margin-top: 20px;
  .goods-article {
    width: 940px;
    margin-right: 20px;
  }
  .goods-aside {
    width: 280px;
    min-height: 1000px;
  }
}
.goods-tabs {
  min-height: 600px;
  background: #fff;
}
.goods-warn {
  min-height: 600px;
  background: #fff;
  margin-top: 20px;
}
</style>
