import { mergeLocalCart, findCartList, insertCart, deleteCart, updateCart, checkAllCart } from '@/api/cart'
// 购物车状态
export default {
  namespaced: true,
  state: () => ({
    list: [] // 购物车列表
  }),
  getters: { // 有效商品
    effectiveList (state) {
      return state.list.filter(item => item.isEffective === true)
    },
    allCount (state, getters) { // 有效商品总数
      return getters.effectiveList.reduce((a, cur) => a + cur.count, 0)
    },
    allPrice (state, getters) { // 有效商品总价
      return getters.effectiveList.reduce((a, cur) => a + cur.count * cur.nowPrice, 0)
    },
    selectedList (state) { // 已选中商品
      return state.list.filter(item => item.selected)
    },
    allSelectedPrice (state, getters) { // 已选中总钱数
      return getters.selectedList.reduce((a, cur) => a + cur.count * cur.nowPrice, 0)
    },
    isAll (state, getters) { // 全选
      if (getters.effectiveList.length === 0) {
        return false
      }
      return getters.effectiveList.every(item => item.selected)
    }
  },
  mutations: {
    insertCart (state, good) { // 添加商品
      state.list.unshift(good)
    },
    delGood (state, good) { // 删除商品
      const index = state.list.findIndex(item => item.skuId === good.skuId)
      state.list.splice(index, 1)
    },
    allChange (state, bool) { // 全选功能
      state.list.forEach(item => { item.selected = bool })
    },
    singleChange (state, { good, bool }) { // 单选按钮
      const item = state.list.find(item => item.skuId === good.skuId)
      item.selected = bool
    },
    changeNum (state, { good, num }) { // 修改数量
      const item = state.list.find(item => item.skuId === good.skuId)
      item.count = num
    },
    changeList (state, newList) {
      state.list = newList
    }
  },
  actions: {
    async mergeCartList (ctx) { // 合并购物车
      const cartList = ctx.state.list.map(item => {
        return {
          skuId: item.skuId,
          selected: item.selected,
          count: item.count
        }
      })
      await mergeLocalCart(cartList)
      const res = await findCartList()
      ctx.commit('changeList', res.result)
    },
    asyncInsertCart (ctx, good) { // 添加
      return new Promise((resolve, reject) => {
        // 已登陆
        if (ctx.rootState.user.profile.token) {
          insertCart({ skuId: good.skuId, count: good.count }).then(() => {
            findCartList().then(res => {
              ctx.commit('changeList', res.result)
              resolve()
            })
          })
        } else {
          // 未登录
          ctx.commit('insertCart', good)
          resolve()
        }
      })
    },
    asyncDelGood (ctx, good) { // 删除
      return new Promise((resolve, reject) => {
        // 已登陆
        if (ctx.rootState.user.profile.token) {
          deleteCart([good.skuId]).then(() => {
            findCartList().then(res => {
              ctx.commit('changeList', res.result)
              resolve()
            })
          })
        } else {
          // 未登录
          ctx.commit('delGood', good)
          resolve()
        }
      })
    },
    asyncAllChange (ctx, bool) { // 全选按钮
      return new Promise((resolve, reject) => {
        // 已登陆
        if (ctx.rootState.user.profile.token) {
          const ids = ctx.getters.effectiveList.map(item => item.skuId)
          checkAllCart({ selected: bool, ids }).then(() => {
            findCartList().then(res => {
              ctx.commit('changeList', res.result)
              resolve()
            })
          })
        } else {
          // 未登录
          ctx.commit('allChange', bool)
          resolve()
        }
      })
    },
    asyncSingleChange (ctx, { good, bool }) { // 单选按钮
      return new Promise((resolve, reject) => {
        // 已登陆
        if (ctx.rootState.user.profile.token) {
          updateCart({
            skuId: good.skuId,
            selected: bool,
            count: good.count
          }).then(() => {
            findCartList().then(res => {
              ctx.commit('changeList', res.result)
              resolve()
            })
          })
        } else {
          // 未登录
          ctx.commit('singleChange', { good, bool })
          resolve()
        }
      })
    },
    asyncChangeNum (ctx, { good, num }) { // 修改数量
      return new Promise((resolve, reject) => {
        // 登录
        if (ctx.rootState.user.profile.token) {
          updateCart({
            skuId: good.skuId,
            selected: good.selected,
            count: num
          }).then(() => {
            findCartList().then(res => {
              ctx.commit('changeList', res.result)
              resolve()
            })
          })
        } else {
          // 非登陆
          ctx.commit('changeNum', { good, num })
          resolve()
        }
      })
    }
  }
}
