<template>
  <div class="goods-image">
    <div class="large"
    :style="[{
      backgroundImage:`url(${imageList[curIndex]})`,
      backgroundPositionX: positionX + 'px',
      backgroundPositionY: positionY + 'px'
      }]"
    v-show="showFlag"
    ></div>
    <div class="middle" ref="target">
      <img :src="imageList[curIndex]" alt="">
      <!-- 蒙层容器 -->
      <div v-show="showFlag" class="layer" :style="{left:left+'px',top:top+'px'}"></div>
    </div>
    <ul class="small">
      <li v-for="(img,i) in imageList" :key="i" :class="{active: i===curIndex}">
        <img :src="img" alt="" @mouseenter="mouseEnter(i)">
      </li>
    </ul>
  </div>
</template>
<script>
import { ref, watch } from 'vue'
import { useMouseInElement } from '@vueuse/core'
export default {
  name: 'GoodsImage',
  props: {
    imageList: {
      type: Array,
      default: () => []
    }
  },
  setup () {
    const curIndex = ref(0)
    const target = ref(null)
    function mouseEnter (index) {
      curIndex.value = index
    }
    const { elementX, elementY, isOutside } = useMouseInElement(target)
    const left = ref(0)
    const top = ref(0)
    const positionX = ref(0)
    const positionY = ref(0)
    // 控制滑块显示隐藏
    const showFlag = ref(false)
    watch([elementX, elementY, isOutside], () => {
      showFlag.value = !isOutside.value
      if (isOutside.value) {
        return false
      }
      // 控制滑块
      if (elementX.value < 100) {
        left.value = 0
      }
      if (elementX.value > 300) {
        left.value = 200
      }

      if (elementX.value > 100 && elementX.value < 300) {
        left.value = elementX.value - 100
      }
      // 纵向控制
      if (elementY.value < 100) {
        top.value = 0
      }
      if (elementY.value > 300) {
        top.value = 200
      }
      if (elementY.value > 100 && elementY.value < 300) {
        top.value = elementY.value - 100
      }
      // 控制大图
      positionX.value = -left.value * 2
      positionY.value = -top.value * 2
    })
    return {
      target,
      curIndex,
      mouseEnter,
      showFlag,
      left,
      top,
      positionX,
      positionY
    }
  }
}
</script>
<style scoped lang="less">
.goods-image {
  width: 480px;
  height: 400px;
  position: relative;
  display: flex;
  .middle {
    width: 400px;
    height: 400px;
    background: #f5f5f5;
  }
  .small {
    width: 80px;
    li {
      width: 68px;
      height: 68px;
      margin-left: 12px;
      margin-bottom: 15px;
      cursor: pointer;
      &:hover,&.active {
        border: 2px solid @xtxColor;
      }
    }
  }
}
 .large {
    position: absolute;
    top: 0;
    left: 412px;
    width: 400px;
    height: 400px;
    z-index: 500;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-repeat: no-repeat;
    background-size: 800px 800px;
    background-color: #f8f8f8;
  }
  .layer {
  width: 200px;
  height: 200px;
  background: rgba(0, 0, 0, 0.2);
  left: 0;
  top: 0;
  position: absolute;
}
</style>
