import { userAccountLogin } from '@/api/user'
// 用户状态
export default {
  namespaced: true,
  state: () => (
    {
      profile: {
        id: '',
        nickname: '',
        avatar: '',
        token: '',
        mobile: ''
      }
    }
  ),
  mutations: {
    setProfile (state, payload) {
      state.profile = payload
    },
    clearProfile (state) {
      state.profile = {}
    }
  },
  actions: {
    async asyncGetProfile (ctx, form) {
      const res = await userAccountLogin(form)
      // console.log(res)
      ctx.commit('setProfile', res.result)
    }
  }
}
